<template>
  <div>
    <div
      class="container-fluid wrap"
      v-if="apod"
    >
      <!-- Header -->
      <div class="row justify-content-between mt-1">
        <div class="col-3">
          {{ about }}
        </div>
        <div class="col-3">
          {{ apod.date }}
        </div>
      </div>
      <!-- Content -->
      <div>
        <div class="mt-3">
          <h1>{{ apod.title }}</h1>
        </div>
        <div class="container">
          <iframe
            v-bind:src="apod.url"
            frameborder="0"
            height="500"
            width="790"
            allowfullscreen
            class="video"
          ></iframe>
        </div>
        <blockquote>
          <textarea-autosize
            readonly
            id="text"
            rows="6"
            cols="50"
            v-model="apod.explanation"
            class="mt-2"
          ></textarea-autosize>
          <footer class="blockquote-footer">Credit: <cite>{{ apod.copyright }}</cite></footer>
        </blockquote>
      </div>
      <button
        onclick="responsiveVoice.speak($('#text').val());"
        type="button"
        value="Play"
        class="btn btn-success"
      >
        Play
      </button>
    </div>
    <div v-else>
      loading...<br> <em>or better check your internet</em>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'APOD',
  data () {
    return {
      about: 'Astronomy Picture of the Day',
      apod: ''
    }
  },
  mounted () {
    axios.get('https://api.nasa.gov/planetary/apod?api_key=vf64R8zpJBXhv3IIw4argAXnALVdrDRLyNzts8lb').then(response => (this.apod = response.data)).catch(error => console.log(error))
  }
}
</script>

<style>
h1 {
  font-size: 1.75rem !important;
}
.wrap {
  max-width: 85vw;
  margin: 2vh auto;
}
img,
iframe {
  max-width: 100%;
  max-height: auto;
}
textarea {
  resize: none !important;
  border-style: none;
  resize: both;
  overflow: auto;
  background-color: #1d1d1d;
  color: #6c757d;
  outline: none;
  max-width: 100%;
  hyphens: auto;
  text-align: justify;
}
.container {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
}
.video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
</style>
